@import url('https://fonts.googleapis.com/css?family=Overlock:400,400i,700&display=swap&subset=latin-ext');
// @import url('https://fonts.googleapis.com/css2?family=Fira+Sans:wght@300;500&display=swap&subset=latin-ext');
@import url('https://fonts.googleapis.com/css?family=Inconsolata:400&display=swap&subset=latin-ext');

$theme-color: #004080;
$theme-contrast: #93A1A1;
$luca-red: #800000;
$luca-green: #008000;
$luca-blue: #000080;

$code-background: #EEE;

///////////
// FONTS //
///////////

$text-font-stack: Overlock, sans-serif;
$code-font-stack: Inconsolata, monospace;

$width:  210mm;
$height: 148mm;
$unit:   5.5mm;
$margin: $width / 30;

$grid-color: #00000010;

html {
    font-family: $text-font-stack;
    font-size: $unit;
    margin: 0;
    line-height: 1;
}

body {
    margin: 0;
}

section {
    background-color: white;
}

pre, code {
    font-family: $code-font-stack;
    padding: 0;
}

em {
    font-style: normal;
    text-decoration: underline;
    text-decoration-thickness: 0.05em;
}

del {
    @extend .bhl;
    text-decoration: none;
}

p.skip {
    padding-top: 1em;
}

.margin-right {
    margin-right: 1rem !important;
}

.right {
    display: block;
    float: right;
}

.centered {
    text-align: center !important;
}

.hl {
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 25%, yellow);
}

.bhl {
    background: #FFFF0040;
    margin: -0.5ex;
    padding: 0.5ex;
    border-radius: 0.5ex;
}

/////////////////////
// GLOBAL SETTINGS //
/////////////////////

.license {
    font-style: normal;
    font-size: 75%;
}

.license-icon {
    float: left;
    margin-right: 0.75 * $unit;
}

a, a:visited {
    color: teal;
    text-decoration: none;
    text-decoration-color: #CCC;
}

a:hover {
    color: white;
    background-color: teal;
    border-radius: 0.5ex;
    margin: 0.1ex -0.25ex;
    padding: 0.1ex 0.25ex;
    text-decoration: none;
}

.smaller {
    font-size: smaller;
}

////////////////////////////////////////////
// OVERRIDE ERRORS IN SYNTAX HIGHLIGHTING //
////////////////////////////////////////////

.highlight .err {
    border: none !important;
}

/////////////
// HEADERS //
/////////////

h1, h2, h3, h4, h5, h6 {
    font-weight: normal;
    color: $theme-color;
    margin: 0;
}

h1 {
    font-size: 200%;
    padding: 0;
}

h2 {
    font-size: 115%;
    margin-top: 0.5 * $unit;
}

h3 {
    font-weight: bold;
    font-size: 100%;
    margin-top: 0.5 * $unit;
}

///////////////
// EMPHASIZE //
///////////////

.blue {
    color: $luca-blue;
}

.red {
    color: $luca-red;
}

.green {
    color: $luca-green;
}

////////////
// BLOCKS //
////////////

.sezione {
    font-weight: bold;
    text-decoration: underline;
}

.block {
    box-shadow: 1px 1px 3px gray;
    background-color: #DDD;
    border-radius: 0.25 * $unit;
    padding: 0.5 * $unit;
    margin: 0.5 * $unit 0;

    & > h2 {
	margin: -0.5 * $unit;
	padding: 0.5 * $unit;
	border-radius: (0.25 * $unit) (0.25 * $unit) 0 0;
	background-color: $luca-red;
	color: white;
    }
}

.block p {
}

///////////
// LISTS //
///////////

li {
    margin: (0.15 * $unit) 0 0 0;
}

ol.bracketed {
    counter-reset: bracketed-list;
}

ol.bracketed > li {
    list-style: none;
}

ol.bracketed > li::before {
    content: "(" counter(bracketed-list) ") ";
    counter-increment: bracketed-list;
    display: inline-block;
    width: 1.5em;
    margin: 0 0.5em 0 -2em;
    text-align: right;
}

//////////
// CODE //
//////////

ul.code {
    background-color: $code-background;
    padding: 0.5ex 2ex;
    border-radius: 1ex;
}

ul.code ul {
    margin: 0;
    padding: 0 0 0 2ex;
}

ul.code li {
    list-style: none;
}

em.comment {
    font-style: normal;
    text-decoration: none;
    color: $luca-green;
    float: right;
}

em.comment::before {
    content: "// ";
}

////////////
// TABLES //
////////////

table {
    border-collapse: collapse;
    margin-left: auto;
    margin-right: auto;
}

tr {
    border-width: 1px;
}

th, td {
    padding: 5px;
    border-style: solid;
    border-width: 1px;
    border-color: gray;
}

.triangolare td:empty {
    border-style: none;
}

.triangolare tbody > tr > td:first-child {
    border-style: none;
}

.triangolare > tbody > tr:last-child > td {
    border-style: none;
}

table.sdd {
    margin: 1em auto;
    border-style: solid none;

    & > thead > tr {
	border-style: none none solid none;
    }

    & > * > tr > td, th {
	border-style: none;
    }

    & > * > tr > td:nth-child(2), th:nth-child(2) {
	padding-left: 1rem;
    }
}

.parsing {
    margin: 1em auto;
}

.parsing * > tr > *:first-child {
    border-width: 1pt 2pt 1pt 1pt;
}

.parsing * > tr:first-child > th {
    border-width: 1pt 1pt 2pt 1pt;
}

.parsing > *:first-child > tr:first-child > *:first-child {
    border-width: 0 1pt 1pt 0;
}

.jvm {
    margin: 1em auto;

    & > thead > tr > th {
	background-color: lightgray;
    }

    & > thead > tr > th:nth-child(1) {
	width: 5cm;
    }

    & > thead > tr > th:nth-child(2) {
	width: 2cm;
    }

    & > thead > tr > th:nth-child(3) {
	width: 2cm;
    }

    & > thead > tr > th:nth-child(4) {
	width: 8cm;
    }
}

///////////
// BOXES //
///////////

.columns {
    display: flex;
    margin: 0;
    padding: 0;

    div {
	flex: auto;
	margin: 0 1rem 0 0;
	padding: 0;
    }

    div:last-of-type {
	margin: 0;
    }

    .half {
	width: 50%;
    }
}

.row {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.column {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

//////////////
// GRAPHICS //
//////////////

.graphviz {
    margin: 1ex auto !important;
    text-align: center !important;
}

//////////////////
// PARSER LL(1) //
//////////////////

#parser-abc > tbody > tr > td:first-child {
    text-align: right;
}

///////////////////////////
// MEDIA-DEPENDENT RULES //
///////////////////////////

.reveal * {
    text-align: left;
}

@media only screen {
    .reveal .slides section {
	// width: $width;
	// height: $height;
	// margin: 0 !important;
	// padding: $margin !important;
    }

    .columns {
	display: flex;
	margin: 0;
	padding: 0;
    }
}

@media print {
    @page {
	size: A5 landscape;
    }

//     html {
// 	font-size: 0.925 * $unit;
//     }

//     body {
// 	background-color: transparent;
//     }

    .reveal .slides section {
	// margin: $margin !important;
	// page-break-after: always;
	// border-style: none;
	// padding: 1cm !important;
    }

    section:last-of-type {
	page-break-after: avoid;
    }

    .block {
	box-shadow: none;
    }
}
